.section {
	@include aspect-ratio {
		@include max-aspect-ratio {
			margin-bottom: vw(70);
		}

		@include min-aspect-ratio {
			margin-bottom: vh(70);
		}
	}

	&--big {
		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-bottom: vw(111);
			}

			@include min-aspect-ratio {
				margin-bottom: vh(111);
			}
		}
	}
}
