.connect-block {
	&__white-bg {
		background-color: $color-white;

		@include aspect-ratio {
			@include max-aspect-ratio {
				padding: vw(74) 20% vw(96);
			}

			@include min-aspect-ratio {
				padding: vh(74) 20% vh(96);
			}
		}
	}

	&__title {
		font-family: $font-proxima-nova;

		a {
			display: inline-block;
			font-weight: 700;
			text-decoration: none;
			text-transform: uppercase;
			color: $color-electric-violet;

			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(23);
					font-size: vw(12);
					line-height: vw(24);
					letter-spacing: vw(0.8);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(23);
					font-size: vh(12);
					line-height: vh(24);
					letter-spacing: vw(0.8);
				}
			}
		}

		h1 {
			margin: 0;
			font-weight: 800;

			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-left: vw(-3);
					font-size: vw(61);
					line-height: vw(60);
					letter-spacing: vw(-0.8);
				}

				@include min-aspect-ratio {
					margin-left: vh(-3);
					font-size: vh(61);
					line-height: vh(60);
					letter-spacing: vh(-0.8);
				}
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-bottom: vw(20);
			}

			@include min-aspect-ratio {
				margin-bottom: vh(20);
			}
		}
	}
}
