.lead {
	position: relative;
	font-family: $font-proxima-nova;
	color: $color-white;

	&__background {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 1;
		overflow: hidden;
		background-color: $color-black;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			opacity: 0.6;

			.is-browser-ie & {
				width: auto;
				height: auto;
				min-width: 100%;
				min-height: 100%;
				position: absolute;
				top: -999px;
				left: -999px;
				right: -999px;
				bottom: -999px;
				margin: auto;

				@include aspect-ratio {

					@include min-aspect-ratio {
						margin-top: 50%;
					}
				}
			}
		}
	}

	&__content {
		position: relative;
		z-index: 2;
		min-height: 100vh;
	}

	&__title {
		span {
			display: block;
			text-transform: uppercase;

			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(11);
					font-size: vw(12);
					line-height: vw(75);
					letter-spacing: vw(1.4);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(11);
					font-size: vh(12);
					line-height: vh(75);
					letter-spacing: vh(1.4);
				}
			}
		}

		h1 {
			margin: 0;
			font-weight: 800;
			font-size: 80px;
			line-height: 90px;

			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-left: vw(-4.6);
					font-size: vw(82);
					line-height: vw(91);
					letter-spacing: vw(-1.4);
				}

				@include min-aspect-ratio {
					margin-left: vh(-4.6);

					font-size: vh(82);
					line-height: vh(91);
					letter-spacing: vh(-1.4);
				}
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				padding-top: vw(246);
			}

			@include min-aspect-ratio {
				padding-top: vh(246);
			}
		}
	}

	&__links {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		border-top: 1px solid rgba($color-white, 0.15);
	}

	&__link {
		display: block;
		border-right: 1px solid rgba($color-white, 0.15);
		text-decoration: none;
		color: $color-white;

		&::before {
			content: "";
			position: absolute;
			left: 4.16667vw;
			display: block;
			background-color: $color-white;
			transition: width 0.2s;

			@include aspect-ratio {
				@include max-aspect-ratio {
					top: vw(-1);
					width: vw(80);
					height: vw(2);
				}

				@include min-aspect-ratio {
					top: vh(-1);
					width: vh(80);
					height: vh(2);
				}
			}
		}

		&--last {
			border-right: none;
		}

		&:hover {
			&::before {
				@include aspect-ratio {
					@include max-aspect-ratio {
						width: vw(200);
					}

					@include min-aspect-ratio {
						width: vh(200);
					}
				}
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-top: vw(-1);
				padding: vw(35) 0 0 4.16667vw;
				height: vw(270);
				font-size: vw(18);
				line-height: vw(24);
			}

			@include min-aspect-ratio {
				margin-top: vh(-1);
				padding: vh(35) 0 0 4.16667vw;
				height: vh(270);
				font-size: vh(18);
				line-height: vh(24);
			}
		}
	}
}
