.header {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 150;

	&__logo {
		position: absolute;
		top: 0;
		display: block;
		background-color: $color-tuna;
		transform: translateZ(0);
		transition: transform 0.3s, left 0.3s, background-color 0.3s, width 0.3s, height 0.3s;

		&:focus,
		&:hover {
			outline: none;
		}

		&:hover {
			background-color: lighten($color-tuna, 15%);
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				left: vw(236);
				width: vw(240);
				height: vw(120);
			}

			@include min-aspect-ratio {
				left: vh(236);
				width: vh(240);
				height: vh(120);
			}
		}
	}

	&__logo-svg {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;

		svg {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}

		&--big {
			opacity: 1;
			transition: opacity 0.3s;

			svg {
				@include aspect-ratio {
					@include max-aspect-ratio {
						top: vw(64);
						width: vw(134);
						height: vw(62);
					}

					@include min-aspect-ratio {
						top: vh(64);
						width: vh(134);
						height: vh(62);
					}
				}
			}
		}

		&--small {
			opacity: 0;
			transition: opacity 0.3s;

			svg {
				fill: $color-athens-gray;

				@include aspect-ratio {
					@include max-aspect-ratio {
						width: vw(49);
						height: vw(26);
					}

					@include min-aspect-ratio {
						width: vh(49);
						height: vh(26);
					}
				}
			}
		}
	}

	&__burger {
		position: absolute;
		right: 0;
		top: 0;
		display: block;
		border: none;
		padding: 0;
		background-color: $color-black;
		transition: background-color 0.3s;

		span {
			position: absolute;
			left: 50%;
			top: 50%;
			margin-top: -1px;
			height: 2px;
			background-color: $color-white;
			transition: background-color 0.3s;

			&::before,
			&::after {
				content: "";
				position: absolute;
				left: 0;
				display: block;
				width: 100%;
				height: 2px;
				background-color: $color-white;
				transform-origin: center center;
				transition: transform 0.3s, top 0.3s, bottom 0.3s;
				transition-delay: 0s, 0.3s, 0.3s;

				.is-browser-ie & {
					transition: none;
				}

				@media (max-width: 780px) {
					height: 1px;
				}
			}

			.is-browser-ie & {
				transition: none;
			}

			&::before {
				@include aspect-ratio {
					@include max-aspect-ratio {
						top: vw(-11);
					}

					@include min-aspect-ratio {
						top: vh(-11);
					}
				}
			}

			&::after {
				@include aspect-ratio {
					@include max-aspect-ratio {
						bottom: vw(-11);
					}

					@include min-aspect-ratio {
						bottom: vh(-11);
					}
				}
			}

			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-left: vw(-16);
					width: vw(32);
				}

				@include min-aspect-ratio {
					margin-left: vh(-16);
					width: vh(32);
				}
			}

			@media (max-width: 780px) {
				height: 1px;
			}
		}

		&:focus {
			outline: none;
		}

		&:hover {
			background-color: lighten($color-black, 10%);
		}

		&.active {
			span {
				background-color: transparent;

				&::before,
				&::after {
					transition-delay: 0.3s, 0s, 0s;
				}

				&::before {
					top: 0;
					transform: rotate(45deg);
				}

				&::after {
					bottom: 0;
					transform: rotate(-45deg);
				}
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				width: vw(120);
				height: vw(120);
			}

			@include min-aspect-ratio {
				width: vh(120);
				height: vh(120);
			}
		}
	}

	&.small {
		.header {
			&__logo {
				left: 0;

				@include aspect-ratio {
					@include max-aspect-ratio {
						width: vw(120);
						height: vw(120);
					}

					@include min-aspect-ratio {
						width: vh(120);
						height: vh(120);
					}
				}
			}

			&__logo-svg {
				&--big {
					opacity: 0;
				}

				&--small {
					opacity: 1;
				}
			}
		}
	}
}
