.main-menu {
	position: fixed;
	left: 0;
	right: 0;
	z-index: 100;
	background-color: $color-black;
	transform: translateX(100%);
	transition: transform 0.8s;
	transition-delay: 0.3s;

	&__personal-link {
		position: absolute;
		z-index: 3;
		display: block;
		border-bottom: 1px solid rgba($color-white, 0.8);
		font-family: $font-proxima-nova;
		font-weight: 800;
		text-decoration: none;
		color: $color-white;
		opacity: 0.7;
		transition: opacity 0.2s, border-bottom-color 0.3s;

		&:hover {
			border-bottom-color: $color-white;
			opacity: 1;
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				right: vw(170);
				top: vw(120);
				font-size: vw(18);
				line-height: vw(18);
			}

			@include min-aspect-ratio {
				right: vh(170);
				top: vh(120);
				font-size: vh(18);
				line-height: vh(18);
			}
		}
	}

	&__content {
		position: relative;
		z-index: 1;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: center;
		height: 100%;
	}

	&__list {
		height: 100%;
		max-height: 100%;
		font-family: $font-proxima-nova;
		font-weight: 800;
		text-decoration: none;
		color: $color-white;

		a {
			text-decoration: none;
			color: $color-white;
			transition: color 0.2s;

			&:hover {
				color: $color-tuna;
			}

			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-left: vw(-1);
					font-size: vw(60);
					line-height: vw(80);
				}

				@include min-aspect-ratio {
					margin-left: vh(-1);
					font-size: vh(60);
					line-height: vh(80);
				}
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				font-size: vw(60);
				line-height: vw(80);
				letter-spacing: vw(-0.3);
			}

			@include min-aspect-ratio {
				font-size: vh(60);
				line-height: vh(80);
				letter-spacing: vh(-0.3);
			}
		}
	}

	&__footer {
		position: absolute;
		left: 0;
		right: 0;
		z-index: 2;
		font-family: $font-proxima-nova;
		font-weight: 300;
		text-transform: uppercase;
		color: $color-white;

		@include aspect-ratio {
			@include max-aspect-ratio {
				bottom: vw(66);
				padding-bottom: vw(40);
				font-size: vw(12);
				line-height: vw(20);
				letter-spacing: vw(0.7);
			}

			@include min-aspect-ratio {
				bottom: vh(66);
				padding-bottom: vh(40);
				font-size: vh(12);
				line-height: vh(20);
				letter-spacing: vh(0.7);
			}
		}
	}

	&__design-by {
		font-family: $font-proxima-nova;
		font-weight: 300;
		text-transform: uppercase;

		a {
			font-weight: 700;
			text-decoration: none;
			color: $color-white;
		}
	}

	&.visible {
		transform: translateX(0);
	}

	@include aspect-ratio {
		@include max-aspect-ratio {
			top: vw(-70);
			bottom: vw(-70);
			padding: vw(70) 0;
		}

		@include min-aspect-ratio {
			top: vh(-70);
			bottom: vh(-70);
			padding: vh(70) 0;
		}

	}
}
