.step-block {
	position: relative;
	flex-shrink: 0;
	border: 2px solid $color-white;
	color: $color-scarpa-flow;

	&::before,
	&::after {
		content: "";
		position: absolute;
		left: 50%;
		z-index: 1;
		display: block;
		border: 2px solid $color-electric-violet;
		height: 0;
		transform: translateX(-50%);
		transition: width 0.3s ease-in, height 0.3s ease-out;
		transition-delay: 0.3s, 0s;

		@include aspect-ratio {
			@include max-aspect-ratio {
				width: vw(80);
			}

			@include min-aspect-ratio {
				width: vh(80);
			}
		}
	}

	&::before {
		top: -2px;
		border-bottom: none;
	}

	&::after {
		bottom: -2px;
		border-top: none;
	}

	&__index {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;

		svg {
			position: absolute;
			left: 50%;
			top: 50%;
			fill: $color-white;
			transform: translate(-50%, -50%);
			transition: fill 0.6s;
			transition-delay: 0.1s;
		}
	}

	&__container {
		position: relative;
		z-index: 2;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
	}

	&__title {
		font-family: $font-proxima-nova;
		font-weight: 800;
		text-align: center;

		@include aspect-ratio {
			@include max-aspect-ratio {
				font-size: vw(28);
				line-height: vw(40);
			}

			@include min-aspect-ratio {
				font-size: vh(28);
				line-height: vh(40);
			}
		}
	}

	&__content {
		text-align: center;

		a {
			border-bottom: 1px solid $color-scarpa-flow;
			text-decoration: none;
			color: $color-scarpa-flow;
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				font-size: vw(28);
				line-height: vw(40);
			}

			@include min-aspect-ratio {
				font-size: vh(28);
				line-height: vh(40);
			}
		}
	}

	&--1 {
		.step-block {
			&__index {
				svg {
					@include aspect-ratio {
						@include max-aspect-ratio {
							width: vw(263);
							height: vw(196);
						}

						@include min-aspect-ratio {
							width: vh(263);
							height: vh(196);
						}
					}
				}
			}
		}
	}

	&--2 {
		.step-block {
			&__index {
				svg {
					@include aspect-ratio {
						@include max-aspect-ratio {
							width: vw(320);
							height: vw(196);
						}

						@include min-aspect-ratio {
							width: vh(320);
							height: vh(196);
						}
					}
				}
			}
		}
	}

	&--2 {
		.step-block {
			&__index {
				svg {
					@include aspect-ratio {
						@include max-aspect-ratio {
							width: vw(323);
							height: vw(196);
						}

						@include min-aspect-ratio {
							width: vh(323);
							height: vh(196);
						}
					}
				}
			}
		}
	}

	&:last-of-type {
		margin-right: 0;
	}

	&:hover {
		&::before,
		&::after {
			transition: width 0.3s ease-out, height 0.3s ease-in;
			transition-delay: 0s, 0.3s;

			@include aspect-ratio {
				@include max-aspect-ratio {
					width: vw(400);
					height: vw(128);
				}

				@include min-aspect-ratio {
					width: vh(400);
					height: vh(128);
				}
			}
		}
	}

	@include aspect-ratio {
		@include max-aspect-ratio {
			flex-basis: vw(400);
			margin-right: vw(40);
			padding: vw(20) vw(40);
			width: vw(400);
			height: vw(320);
		}

		@include min-aspect-ratio {
			flex-basis: vh(400);
			margin-right: vh(40);
			padding: vw(40) vw(40);
			width: vh(400);
			height: vh(320);
		}
	}
}
