.steps {
	&__title {
		font-family: $font-proxima-nova;
		font-weight: 800;

		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-bottom: vw(50);
				margin-left: vw(-1);
				font-size: vw(28);
				line-height: vw(38);
				letter-spacing: vw(-0.12);
			}

			@include min-aspect-ratio {
				margin-bottom: vh(50);
				margin-left: vh(-1);
				font-size: vh(28);
				line-height: vh(38);
				letter-spacing: vh(-0.12);
			}
		}
	}

	&__list {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;

		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-bottom: vw(50);
			}

			@include min-aspect-ratio {
				margin-bottom: vh(50);
			}
		}
	}
}
