.content-area {
	font-family: $font-houschka-pro;
	font-weight: 300;
	color: $color-scarpa-flow;

	b {
		font-weight: 600;
	}

	p {
		margin: 0;

		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-bottom: vw(10);
			}

			@include min-aspect-ratio {
				margin-bottom: vh(10);
			}
		}
	}

	@include aspect-ratio {
		@include max-aspect-ratio {
			font-size: vw(22);
			line-height: vw(32);
			letter-spacing: vw(-0.2);
		}

		@include min-aspect-ratio {
			font-size: vh(22);
			line-height: vh(32);
			letter-spacing: vh(-0.2);
		}
	}
}
